


.welcome h3 {
    font-weight: 800;
    color: $purple;
    font-size: 1.75rem;
    margin-bottom: 30px;
}

.welcome p {
    font-size: 1.2rem;
} 


.services {
    display: flex;
    flex-direction: column;
    background-color: $purple;
    width: 100%;
    color: white;
    padding: 56px 0 60px;
}


.services h3 {
    margin-bottom: 1.5rem;
    margin-top: 0;
    font-size: 2rem;
}

.services hr {
    background-color: #fff;
    width: 150px;
    height: 2px;
    margin-left: 0;
    margin-right: auto;
}

.services p {
    margin: 0 0 16px;
    padding-top: 8px;
}

.service-text {
    flex-direction: row;
    flex-wrap: wrap;
}

.service-card {
    margin-bottom: 3rem;
}

.service-card-content {
    background: white;
}

.service-card-img-1,
.service-card-img-2,
.service-card-img-3 {
    width: 100%;
    padding-top: 68%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
}

.service-card-img-1 {
    background-image: url("../assets/imgs/apprentice.jpg");
}

.service-card-img-2 {
    background-image: url("../assets/imgs/globe.jpg");
}

.service-card-img-3 {
    background-image: url("../assets/imgs/bespoke.jpg");
}

.icon {
    width: 92px;
    height: 92px;
    position: absolute;
    top: -60px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50%;
    background-color: $purple;
}

.fas,
.far {
    font-size: 50px;
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%,50%);
}

.service-card-text h4 {
    text-transform: uppercase;
    font-size: 26px;
    margin-bottom: 8px;
    margin-top: 0;
    color: $purple;
    font-weight: 800;
}

.service-card-text p {
    font-size: 17px;
    padding-top: 8px;
    margin-bottom: 16px;
    color: black;
}

.service-card-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    text-align: left;
    padding: 55px 25px 20px;
    position: relative;
}

.secondary-info {
    margin: 56px 0 40px;
}

.secondary-info h3 {
    font-size: 1.75rem;
    color: $purple;
    font-weight: 800;
    margin-bottom: 30px;
}

.secondary-info p {
    font-size: 19px;
    margin-bottom: 1rem;
    margin-top: 0;
}


.get-in-touch p {
    color: white;
    font-size: 18px;
}

.get-in-touch {
    background: $purple;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding: 40px 0;
}

.get-in-touch-btn {
    display: flex;
    justify-content: center;
    text-align: center;
    background-color: #fff;
    font-weight: 800;
    font-size: 24px;
    margin-left: auto;
    margin-right: auto;
    padding: 9px 34px;
    border-radius: 7px;
}