// Store any styles that are used across multiple pages such as banners or cards.

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

.secondary-container {
    background: #584096;
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 40px 15px;
    font-size: 18px;
    color: #fff;
}

.secondary-container p {
    max-width: 540px;
}

.internal-page-banner {
    background-size: cover;
    height: 537.6px;
    background-repeat: no-repeat;
}

.info-container h1 {
    font-size: 36px;
    margin: 36px 0;
    color: $purple;
}

.info-container p {
    font-size: 22px;
    margin: 0 0 16px;
    padding: 16px 0 0;
}

.info-container {
    display: flex;
    flex-direction: column;
}